
@import url("https://fonts.googleapis.com/css?family=Varela+Round");
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1
}
ol,ul{list-style:none
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
::-webkit-input-placeholder{font-family:'Varela Round', Helvetica, Arial, sans-serif;color:#cacaca
}
::-moz-placeholder{font-family:'Varela Round', Helvetica, Arial, sans-serif;color:#cacaca
}
:-ms-input-placeholder{font-family:'Varela Round', Helvetica, Arial, sans-serif;color:#cacaca
}
:-moz-placeholder{font-family:'Varela Round', Helvetica, Arial, sans-serif;color:#cacaca
}
body{overflow:hidden;background:#1bb0f4
}
#app{font-family:'Varela Round', Helvetica, Arial, sans-serif;font-size:14px;font-weight:600;color:#333;position:relative;height:500px;max-width:350px;padding-top:30px;margin:60px auto;margin-top:60px;overflow:hidden;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;background:#1bb0f4;border-radius:5px
}
#app:before{position:absolute;top:0;z-index:2;display:block;width:70%;height:80%;margin-top:38px;margin-left:15%;background:#fff;border-radius:5px;content:'';opacity:0.1
}
#app:after{position:absolute;top:0;z-index:1;display:block;width:75%;height:76%;margin-top:50px;margin-left:12%;background:#55c7f7;border-radius:5px;content:''
}



